import { v4 as uuidv4 } from 'uuid';
//  
function thorottling(fun, delay) {
  let clock = true;
  return function () {
    let that = this;
    let args = arguments;
    if (clock) {
      fun.call(that,...args);
      clock = false;
    }
    setTimeout(() => {
      clock = true;
    }, delay);
  };
};

 //
function debounce(fun, delay) {
  console.log('denghao');
  let timer = null;
  return function debounced() {
    // this
    let that = this;
    let args = arguments;
    clearTimeout(timer);
    timer = setTimeout(() => {
      fun.call(that, ...args);
    }, delay);
  };
}

// 
function getUuid(){
  // 
  let uuid_token = localStorage.getItem('UUIDTOKEN');
  // 
  if(!uuid_token){
    // 
    uuid_token = uuidv4();
    localStorage.setItem('UUIDTOKEN',uuid_token);
  }
  return uuid_token;
}
export {thorottling,debounce,getUuid}