<template>
  <!--  -->
  <div class="type-nav">
    <div class="container">
      <div @mouseleave="leaveIndex" @mouseenter="enterShow">
        <h2 class="all"></h2>
        <!--  -->
        <transition name="sort">
          <div class="sort" v-show="show">
            <!--  -->
            <div class="all-sort-list2" @click="goSearch">
              <!--  -->
              <div
                class="item"
                v-for="(c1, index) in categoryList"
                :key="c1.categoryId"
              >
                <h3 @mouseenter="changeIndex(index)" :class="{cur:currentIndex == index}" style="cursor:pointer">
                  <a
                    :data-categoryName="c1.categoryName"
                    :data-categoryId1="c1.categoryId"
                    >{{ c1.categoryName }}</a
                  >
                  <!-- router-link -->
                  <!-- <router-link to="/search">{{ c1.categoryName }}</router-link> -->
                </h3>
                <!--  -->
                <div class="item-list clearfix" v-show="index == currentIndex">
                  <!--  -->
                  <div
                    class="subitem"
                    v-for="c2 in c1.categoryChild"
                    :key="c2.categoryId"
                  >
                    <dl class="fore">
                      <dt>
                        <a
                          style="cursor: pointer"
                          :data-categoryName="c2.categoryName"
                          :data-categoryId2="c2.categoryId"
                          >{{ c2.categoryName }}</a
                        >
                        <!-- <router-link to="/search">{{ c2.categoryName }}</router-link> -->
                      </dt>
                      <dd>
                        <!--  -->
                        <em v-for="c3 in c2.categoryChild" :key="c3.categoryId">
                          <a
                            style="cursor: pointer"
                            :data-categoryName="c3.categoryName"
                            :data-categoryId3="c3.categoryId"
                            >{{ c3.categoryName }}</a
                          >
                        </em>
                      </dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <nav class="nav">
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
        <a href="###"></a>
      </nav>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
// loadsh
import { throttle } from "lodash";
import { thorottling, debounce } from "@/utils";
export default {
  name: "TypeNav",
  data() {
    return {
      // 
      currentIndex: -1,
      show: true,
    };
  },
  mounted() {
    // ,search
    if (this.$route.path != "/home") {
      this.show = false;
    }
  },
  computed: {
    ...mapState({
      // 
      // categotyList:state => state.home.categoryList,
      categoryList: (state) => {
        return state.home.categoryList;
      },
    }),
    // ...mapState('home',['categoryList'])
  },

  methods: {
    // currentIndex
    /*  changeIndex(index){
    // h3
    // 
    // 
    // 
    // 
    this.currentIndex = index;
  }, */
    changeIndex: thorottling(function (index) {
      this.currentIndex = index;
    }, 100),
    // currentIndex
    leaveIndex() {
      this.currentIndex = -1;
      if (this.$route.path != "/home") {
        this.show = false;
      }
    },
    // 
    goSearch(e) {
      // 
      // dom
      let element = e.target;
      // dataset
      let { categoryname, categoryid1, categoryid2, categoryid3 } =
        element.dataset;
      // a
      if (categoryname) {
        // 
        // 
        let location = { name: "search" };
        let query = { categoryName: categoryname };
        if (categoryid1) {
          // 
          query.category1Id = categoryid1;
        } else if (categoryid2) {
          // 
          query.category2Id = categoryid2;
        } else {
          // 
          query.category3Id = categoryid3;
        }
        // 
        location.query = query;
        // 
        //  params
        if(this.$route.params){
          location.params = this.$route.params;
          this.$router.push(location);
        }
      }
      // categorynamea
      /*  this.$router.push({
        name:'search',
      }) */
    },
    // 
    enterShow() {
      if (this.$route.path != "/home") {
        this.show = true;
      }
    },
  },
};
</script>

