import VueRouter from "vue-router";
import Vue from "vue";
import routes from './routes'
import store from '@/store'
// console.log(VueRouter);
let originPush = VueRouter.prototype.push;
let originRepalce = VueRouter.prototype.replace;
// pushrepalce(resolve,reject)
// push
VueRouter.prototype.push = function (location, resolve, reject) {
  if (resolve & reject) {
    // callapply
    // callapplycallapply
    originPush.call(this, location, resolve, reject);
  } else {
    originPush.call(this, location, () => { }, () => { });
  }
}

VueRouter.prototype.replace = function (location, resolve, reject) {
  if (resolve & reject) {
    // callapply
    // callapplycallapply
    originRepalce.call(this, location, resolve, reject);
  } else {
    originRepalce.call(this, location, () => { }, () => { });
  }
}

const router = new VueRouter({
  // 
  routes,
  // 
  scrollBehavior(to, from, savedPosition) {
    // 
    return { y: 0 }
  },
})


// 
router.beforeEach((to, from, next) => {
  let token = store.state.users.token;
  let name = store.state.users.userInfo.name;
  if (token) {
    //,login
    if (to.path == '/login') {
      //home
      next('/home');
    } else {
      console.log('');
      if (name) {
        next();
      } else {
         // 
          store.dispatch('getUserInfo').then(res => {
            console.log(res);
            next();
          }).catch(e => {
            //token
            localStorage.clear();
            next('/login');
          });
      }
    }
  } else {
    // 
    next();
  }
})



export default router;