// 
import Home from '../pages/Home';
import Search from '../pages/Search'
import Login from '../pages/Login'
import Register from '../pages/Register'
import Detail from '../pages/Detail'
import AddCartSuccess from '../pages/AddCartSuccess'
import ShopCart from '@/pages/ShopCart'
import Trade from '@/pages/Trade'
import Pay from '@/pages/Pay'
export default [
  {
    path:'/home',
    component:Home,
    meta:{show:true}
  },
  {
    path:'/search/:keyword?',
    component:Search,
    meta:{show:true},
    name:'search',
    // 1.:params
    // props:true,
    // 2.:props
    // props:{a:1,b:2},
    // 3.paramsquery
    props($route){
      return {
        keyword:$route.params.keyword,
        key:$route.query.key
      }
    }
  },
  {
    name:'login',
    path:'/login',
    component:Login,
    meta:{show:false}
  },
  {
    path:'/register',
    component:Register,
    meta:{show:false}
  },
  { 
    name:'detail',
    path:'/detail/:skuId',
    component:Detail,
    meta:{show:true},
    props($route){
      return {
        skuid:$route.params.skuId
      }
    }
  },
  // 
  {
    name:'AddCartSuccess',
    path:'/AddCartSuccess',
    component:AddCartSuccess,
    meta:{show:true}
  },
  // 
  {
    name:'shopCart',
    path:'/shopCart',
    component:ShopCart,
    meta:{show:true}
  },
  // 
  {
    name:'trade',
    path:'/trade',
    component:Trade,
    meta:{show:true}
  },
  // 
  {
    name:'pay',
    path:'/pay',
    component:Pay,
    meta:{show:true}
  },
  // /
  {
    path:'*',
    redirect:'/home'
  },
]